* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-row {
  flex-direction: row;
}

.navList-small {
  transition: width 350ms ease-out;
  width: 64px;
}
.navList-big {
  transition: width 350ms ease-out;
  width: 260px;
}

.navIcon {
  padding: 3px;
  padding-left: 16px;
  height: 48px;
}

.navTitleBox {
  height: 68px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.details-area {
  background: red;
}/*# sourceMappingURL=main.css.map */